let infoBox = [];
function put_in_infoBox(action){
    // console.log(action);
    //infoBox
    infoBox.push(action)
}

function addPro(state,action){
    // console.log(action);
    // console.log(action.type);
    if (!state) {
        return {
            pro_name: [],
            pro_price: [],
        }
    }
    switch(action.type){
        //action,infoBox
        case 'ADD': 
            //
            put_in_infoBox(action);
            console.log('infoBox-content')
            console.log(infoBox);
            return {
                pro_Info:infoBox

            }
        default:
            return state
    }


}

export default addPro